import React, { useReducer } from 'react';
import axios from 'axios';
import MovieContext from './MovieContext';
import MovieReducer from './MovieReducer';
import {
	SEARCH_MOVIES,
	GET_MOVIE,
	CLEAR_MOVIES,
	SET_LOADING
} from '../Types';





const MovieState = props => {
	const initialState = {
		movies: [],
		movie: {},
		loading: false
	}
	
	const [state, dispatch] = useReducer(MovieReducer, initialState);
	const movieApiKey=process.env.REACT_APP_API_KEY;


	const searchMovies = async text => {
		setLoading();
		const res = await axios.get(`https://www.omdbapi.com/?s=${text}&apikey=${movieApiKey}`);
		dispatch({
			type: SEARCH_MOVIES,
			payload: res.data.Search || res.data.Response
		});
	};
	
	const getMovie = async (movieName) => {
		setLoading();
		const res = await axios.get(`https://www.omdbapi.com/?t=${movieName}&apikey=${movieApiKey}`);
		console.log(res.data);
		dispatch({
			type: GET_MOVIE,
			payload: res.data
		});
	};
	
	const clearMovies = () => {
		dispatch({
			type: CLEAR_MOVIES
		})
	};
	
	const setLoading = () => dispatch({
		type: SET_LOADING
	});
	
	
	//Make values available to whole app
	return <MovieContext.Provider
		value={{
			movies: state.movies,
			movie: state.movie,
			loading: state.loading,
			searchMovies,
			clearMovies,
			getMovie
		}}
	>
		{props.children}
	</MovieContext.Provider>
}

export default MovieState;
	