import React, { useEffect, useContext } from 'react';
import MovieContext from '../../../context/movies/MovieContext';
import Spinner from '../../layout/Spinner';

const Movie = ({ match }) => {
		const movieContext = useContext(MovieContext);
		
		const { movie, getMovie, loading } = movieContext;
		
		useEffect(() => {
			getMovie(match.params.title);
			//eslint-disable-next-line
		}, []);
		
		const {
			Title, 
			Year,
			Poster,
			Plot,
			Actors,
			Director,
			Language,
			Runtime,
			Genre
		} = movie;
			
		if(loading) {
			return <Spinner />
		} else {
			return (
				<>
					
					<div style={cardStyle} className="card teal lighten-4">
						<div className="card-content">
							<div className="card-title">
								<h3><u>{Title}</u></h3>
							</div>
							<div className="responsive-img">
								<img src={Poster} alt={`${Poster}`} />
							</div>
							<p className="card-text"><strong>{Plot}</strong></p>
							<hr />
							<p><strong>Release Year: </strong> {Year}</p>
							<hr />
							<p><strong>Actors/Actresses: </strong> {Actors}</p>
							<hr />
							<p><strong>Director: </strong>{Director}</p>
							<hr />
							<p><strong>Language: </strong>{Language}</p>
							<hr />
							<p><strong>Runtime: </strong>{Runtime}</p>
							<hr />
							<p><strong>Genre: </strong>{Genre}</p>
						</div>
					</div>
					<br />
					<br />
				</>
				)
	}
			
		
}

const cardStyle = {
	margin: '40px auto auto auto',
	width: '60%',
	textAlign: 'center'
}


	
export default Movie;