import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import './MovieItemStyles.css';

const MovieItem  = ({ movie: { Poster, Title, Year } }) => {
	console.log(Poster)
	return (
		<div className='card' style={cardStyles}>
			{Poster ? ( 
					<img src={Poster} alt="" style={{ width: '80%' }}/>
			) : (
					<img src={'https://commons.wikimedia.org/wiki/Category:Movie_posters_placeholders#/media/File:No_image_poster.png'} alt="" style={{ width: '90px' }}/>
			)}
			
			<h3 className="card-title">{Title}</h3>
			<div>
				<Link to={`/movie/${Title}`} className="waves-effect waves-light btn">
					More
				</Link>
			</div>
		</div>
	);
};

MovieItem.propTypes = {
	movie: PropTypes.object.isRequired
};

const cardStyles = {
	textAlign: 'center'
}



export default MovieItem