import React, {useState, useContext } from 'react';
import MovieContext from '../../context/movies/MovieContext';
import AlertContext from '../../context/alert/AlertContext';


const Search = () => {
	const movieContext = useContext(MovieContext)
	const alertContext = useContext(AlertContext)
	
	const [text, setText] = useState('');
	
	const handleSearchInput = (e) => {
		setText(e.target.value);
	}
	
	const onSubmit = e => {
		e.preventDefault();
		if(text === '') {
			alertContext.setAlert('Please enter something to search for', 'light')
		}
		else {
			movieContext.searchMovies(text);
			setText('')
		}
	};
	
	return (
		<>
			<div style={infoStyle}>
				Just type in a word/phrase below to see all of the films/series listed on the 
				<a target="blank" href="http://www.omdbapi.com/"> OMDB API </a> 
				containing that word.
			</div>
			<form onSubmit={onSubmit} style={formStyles}>
				<input
					value={text}
					onChange={handleSearchInput}
					type="text"
					autoFocus
					style={{textAlign:'center'}}
				/>
				<input 
					type="submit"
					value="Search"
				/>
				<br />
				<br />
				{ movieContext.movies.length > 0 && (
				<button className="btn" onClick={movieContext.clearMovies}>
					Clear Movies
				</button>
			)}
			</form>
		</>
	)
}

const formStyles = {
	width: '300px',
	margin: 'auto',
	textAlign: 'center'
}

const infoStyle = {
	fontWeight: 'bold',
	textAlign: 'center',
	width: '40%',
	margin: '10px auto auto auto'
}

export default Search;	
	
	