import React from 'react';

const NotFound = () => {
	return (
		<div style={notFoundStyles}>
			<h1>
				Page Not Found
			</h1>
			<p className='lead'>
				The page you are looking for does not exist
			</p>
			
		</div>
	)
}

const notFoundStyles = {
	textAlign: 'center',
};

export default NotFound
			