import React, { useContext } from 'react';
import MovieItem from './MovieItem';
import MovieContext from '../../../context/movies/MovieContext';
import Spinner from '../../layout/Spinner';

const Movies = () => {
	const movieContext = useContext(MovieContext);
	const {  loading, movies } = movieContext;



	if(loading) {
		return <Spinner />

	}  	
	
	else if (movies.length === 5) {
		return (
			<div style={alertStyle}>
				<h4>Sorry :(</h4>  <br /><strong>We&#39;re sorry, couldn&#39;t find 
				any films containing the word you searched for.<br /> 
				Please try searching for something else.</strong>
			</div>
		)
	}
	else {
			return (
				<>				
					<div style={movieGrid}>
						{movies.map(movie => (
							<MovieItem key={movie.imdbID} movie={movie}/>
						))}
					</div>
				</>
			)
			
		
	}
	
};

const movieGrid = {		
	display: 'grid',
	gridTemplateColumns: 'repeat(3, 1fr)',
	gridGap: '10px',
	justifyContent: 'center',
	width: '95%',
	margin: 'auto'
}

const alertStyle = {
	backgroundColor: '#FFCCCC',
	color: 'black',
	margin: 'auto',
	width: '50%',
	textAlign: 'justify',
	textAlignLast: 'center',
	borderRadius: '15px',
	padding: '10px'
}

export default Movies;
